@media (min-width: 800px) {
    .welcome-mobile {
        display: none;
    }
}

@media (max-width: 800px) {
    .welcome-mobile {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        align-content: center;

        width: 100%;
        height: 100vh;
        background-color: rgba(255, 190, 24, 1);
        font-family: "Rubik", sans-serif;
        font-size: 1.5rem;
    }

    .welcome-mobile div {
        padding: 2rem;
        width: 75%
    }
}

@media (max-width: 801px) {
    .main-desktop {
        display: none;
    }
}
